<template>
    <div class="app-container" v-loading="isLoad" >
        <el-form :label-position="labelPosition" label-width="80px" :model="form" ref='form' style="width:500px ;margin:0 auto;">
            <el-form-item label="自管理门户账号:" prop="account">
                <el-input v-model="form.account" disabled></el-input>
            </el-form-item>
            <el-form-item label="自管理门户密码:" prop="password">
                <el-input v-model="form.password" disabled></el-input>
            </el-form-item>
            <el-form-item label="电信API接口密码:" prop="key">
                <el-input v-model="form.key" disabled></el-input>
            </el-form-item>
            <el-form-item label="折扣(小数点两位):" prop="discount">
                <el-input v-model="form.discount" disabled></el-input>
            </el-form-item>
        </el-form>
    </div>
</template>

<script>
/* eslint-disable quotes,indent,keyword-spacing,spaced-comment,no-var */

import {
    getManufacturerInfoByLogin
} from '../../../api/admin/basicData';
import { mapActions } from 'vuex'
// import { setLoad } from 'store/modules/loading'
export default {
    data() {
        return {
            labelPosition: 'left',
            itemList: [],
            form: {
                id: '',
                account: '',
                password: '',
                key: '',
                discount: ''

            }
        }
    },
    computed: {
        isLoad() {
            return this.$store.state.loading.load
        }
    },
    watch: {},
    mounted() {
        this.manufacturerInfoByLogin();
    },
    methods: {
        ...mapActions(["setLoad"]),
        manufacturerInfoByLogin() {
            this.isLoad=true
            getManufacturerInfoByLogin()
                .then(res => {
                    this.isLoad=false
                    if (res.msg == 0) {
                        this.form.account = res.account;
                        this.form.password = res.password;
                        this.form.key = res.key;
                        this.form.discount = res.discount;
                        res.discount ? this.isdisabled = true : this.isdisabled = false
                    }
                }).catch(() => {
                    this.isdisabled = true
                });
        }
    }
};
</script>

<style scoped>
.app-container {
    background: #f5f5f5;
}

.item-left {
    padding: 7px 0;
}
</style>


